import "./style.scss";

/**
 * Progress bar
 *
 * @param value
 * @param className
 * @returns {JSX.Element}
 * @constructor
 */
export function ProgressBar({value, className = ''}) {
    let style = {};
    let indicatorClassname = 'plover-component-progress-bar__indicator';

    if (value) {
        style['--plover-component-progress-bar-indicator-width'] = Number(value) + '%';
        indicatorClassname += ' has-value';
    }

    return (
        <div className={`plover-component-progress-bar__track ${className}`} style={style}>
            <div className={indicatorClassname}/>
        </div>
    )
}
