/**
 * WordPress dependencies.
 */
import {
  BaseControl,
  __experimentalHStack as HStack, Button,
} from '@wordpress/components';
import { desktop, tablet, mobile } from '@wordpress/icons';
import { __ } from '@wordpress/i18n';

/**
 * Plover dependencies.
 */
import { promoteScalarValueIntoResponsive, getScalarValueByDevice } from '@plover/utils';
import { useDeviceType } from '@plover/data';

// control styles
import './style.scss';

export function ResponsiveControl ({ title, help, value, sanitizer, onChange, Component, controlProps, ...props }) {

  if (!Component) {
    return null;
  }

  const [deviceType, setPreviewDeviceType] = useDeviceType();
  const responsiveValue = getScalarValueByDevice(value, deviceType);

  return (
    <BaseControl className="plover-responsive-control" {...props}>
      <BaseControl.VisualLabel style={{ display: 'block' }} help="xxxx">
        <HStack stretch="space-between" justify="center" className="plover-responsive-control__title">
          <label className="plover-responsive-control__label">{title}</label>

          <div className="plover-responsive-control__devices">
            <Button
              label={__('Desktop mode', 'plover')}
              icon={desktop}
              onClick={() => setPreviewDeviceType('desktop')}
              isPressed={deviceType === 'desktop'}
              isSmall
              className="plover-responsive-control__desktop-device"
              iconSize={24}
            />
            <Button
              label={__('Tablet mode', 'plover')}
              icon={tablet}
              onClick={() => setPreviewDeviceType('tablet')}
              isPressed={deviceType === 'tablet'}
              isSmall
              className="plover-responsive-control__tablet-device"
              iconSize={24}
            />
            <Button
              label={__('Mobile mode', 'plover')}
              icon={mobile}
              onClick={() => setPreviewDeviceType('mobile')}
              isPressed={deviceType === 'mobile'}
              isSmall
              className="plover-responsive-control__mobile-device"
              iconSize={24}
            />
          </div>
        </HStack>
      </BaseControl.VisualLabel>

      {help && <p className="components-base-control__help plover-responsive-control__help">{help}</p>}

      {<Component
        value={sanitizer ? sanitizer(responsiveValue) : responsiveValue}
        onChange={(newResponsiveValue) => {
          onChange({
            ...promoteScalarValueIntoResponsive(value),
            [deviceType]: sanitizer ? sanitizer(newResponsiveValue) : newResponsiveValue,
          });
        }}
        {...controlProps}
      />}
    </BaseControl>
  );
}