/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * WordPress dependencies
 */
import {
  __experimentalHStack as HStack,
  __experimentalZStack as ZStack,
  __experimentalDropdownContentWrapper as DropdownContentWrapper,
  ColorIndicator,
  ColorPalette,
  Flex,
  FlexItem,
  Dropdown,
  Button,
} from '@wordpress/components';
import { __, sprintf } from '@wordpress/i18n';

/**
 * Plover dependencies
 */
import { useSettings } from '@plover/utils';

/**
 * Component style
 */
import './style.scss';

const popoverProps = {
  placement: 'left-start',
  offset: 36,
  shift: true,
};

const LabeledColorIndicators = ({ indicators, label }) => (
  <HStack justify="flex-start">
    <ZStack isLayered={false} offset={-8}>
      {indicators.map((indicator, index) => (
        <Flex key={index} expanded={false}>
          <ColorIndicator colorValue={indicator}/>
        </Flex>
      ))}
    </ZStack>
    {label && <FlexItem
      className="block-editor-panel-color-gradient-settings__color-name"
      title={label}
    >
      {label}
    </FlexItem>}
  </HStack>
);

export function ColorPopover ({ value, onChange, label, excludePresets, enableAlpha, className }) {

  const [colors] = useSettings('color.palette');
  const presetValue = colors.find(({ slug }) => `var(--wp--preset--color--${slug})` === value);

  return (

    <Dropdown
      popoverProps={popoverProps}
      className={className + ' block-editor-tools-panel-color-gradient-settings__dropdown'}
      renderToggle={({ onToggle, isOpen }) => {
        const toggleProps = {
          onClick: onToggle,
          className: clsx(
            'block-editor-panel-color-gradient-settings__dropdown',
            { 'is-open': isOpen }
          ),
          'aria-expanded': isOpen,
          'aria-label': sprintf(
            /* translators: %s is the type of color property, e.g., "background" */
            __('Color %s styles', 'plover'),
            label ?? ''
          ),
        };

        return (
          <Button {...toggleProps}>
            <LabeledColorIndicators
              indicators={[presetValue ? presetValue.color : value]}
              label={label}
            />
          </Button>
        );
      }}
      renderContent={() => (
        <DropdownContentWrapper
          paddingSize="none"
          className="block-editor-panel-color-gradient-settings__dropdown-content"
        >
          <div className="block-editor-color-gradient-control__panel">
            <ColorPalette
              enableAlpha={enableAlpha}
              colors={colors.filter((c) => {
                return (excludePresets || []).indexOf(c.slug) === -1;
              })}
              value={presetValue ? presetValue.color : value}
              onChange={(v) => {
                const preset = colors.find(({ color }) => color === v);
                if (preset) {
                  return onChange(`var(--wp--preset--color--${preset.slug})`);
                }
                return onChange(v);
              }}
            />
          </div>
        </DropdownContentWrapper>
      )}
    />
  );
}
