/**
 * WordPress dependencies
 */
import {
    FlexItem,
    Dropdown,
    __experimentalHStack as HStack
} from "@wordpress/components";
import {Icon} from '@wordpress/icons';

/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * Plover dependencies.
 */
import {isString} from "@plover/utils";

/**
 * Component style
 */
import "./style.scss";

const defaultPopoverProps = {
    placement: 'left-start',
    offset: 36,
    shift: true,
};

export function renderToggleButton({icon, label}) {
    return ({onToggle, isOpen}) => {
        const toggleProps = {
            onClick: onToggle,
            className: clsx({
                'plover-component-popover__toggle': true,
                'is-open': isOpen
            }),
            'aria-expanded': isOpen,
        };

        return (
            <button {...toggleProps}>
                <HStack justify="flex-start">
                    {icon && (
                        !isString(icon) ? <Icon
                            icon={icon}
                            size={28}
                        /> : (
                            <span
                                className="plover-component-popover__toggle-svg-icon"
                                dangerouslySetInnerHTML={{__html: icon}}
                            />
                        )
                    )}
                    <FlexItem>{label}</FlexItem>
                </HStack>
            </button>
        );
    }
}

export function Popover(props) {
    const {
        toggle,
        popoverProps,
        contentClassName,
        className,
        ...extraProps
    } = props;

    return (
        <Dropdown
            contentClassName={"plover-component-popover__content " + (contentClassName ?? '')}
            className={"plover-component-popover__toggle-wrapper " + (className ?? '')}
            popoverProps={popoverProps || defaultPopoverProps}
            renderToggle={renderToggleButton(toggle)}
            {...extraProps}
        />
    )
}
