/**
 * WordPress dependencies
 */
import {useEffect, useRef} from "@wordpress/element";
import {BaseControl} from "@wordpress/components";

import "./style.scss";

export function CodeEditor({value, onChange, className, height, mode = 'javascript', codeMirrorProps = {}, ...props}) {

    const textArea = useRef(null);

    useEffect(() => {
        if (wp.CodeMirror && textArea.current) {
            var editor = wp.CodeMirror.fromTextArea(textArea.current, {
                mode,
                lineNumbers: true,
                ...codeMirrorProps
            });

            editor.on('change', (cm, change) => {
                onChange(cm.getValue());
            });
        }

    }, []);

    return (
        <BaseControl
            className={"plover-component-code-editor " + (className ?? '')}
            {...props}
        >
            <div
                className={"plover-component-code-editor-wrap"}
                style={{
                    '--plover-component-code-editor__height': (height ?? '300px')
                }}
            >
                <textarea ref={textArea} defaultValue={value}/>
            </div>
        </BaseControl>
    );
}
