/**
 * WordPress dependencies
 */
import {
    BaseControl,
    RangeControl,
    __experimentalParseQuantityAndUnitFromRawValue as parseQuantityAndUnitFromRawValue,
    __experimentalUseCustomUnits as useCustomUnits,
    __experimentalUnitControl as UnitControl,
} from "@wordpress/components";
import {useState} from "@wordpress/element";

/**
 * Plover dependencies
 */
import {useSettings} from "@plover/utils";

/**
 * Component style
 */
import "./style.scss";

export function UnitSlider(props) {

    const {label, value, onChange, units, min, max, step} = props;

    // Tracking selected units via internal state allows filtering of CSS unit
    // only values from being saved while maintaining preexisting unit selection
    // behaviour. Filtering CSS unit only values prevents invalid style values.
    const [selectedUnit, setSelectedUnit] = useState(typeof value === 'string'
        ? (parseQuantityAndUnitFromRawValue(value)[1] ?? 'px')
        : 'px');

    const [availableUnits] = useSettings('spacing.units');
    const controlUnits = useCustomUnits({
        availableUnits: units || availableUnits || ['px', 'em', 'rem'],
    });

    const unitConfig = controlUnits && controlUnits.find((item) => item.value === selectedUnit);
    const sliderStep = step ?? unitConfig?.step ?? 1;
    const sliderMin = min ?? unitConfig?.min ?? -100;
    const sliderMax = max ?? unitConfig?.max ?? 100;

    const handleSliderChange = (next) => {
        onChange(next !== undefined ? `${next}${selectedUnit}` : undefined);
    }

    return (
        <fieldset className="plover-components-unit-slider-control">
            {label && (
                <BaseControl.VisualLabel as="legend">
                    {label}
                </BaseControl.VisualLabel>
            )}
            <div className="plover-components-unit-slider-control__wrapper">
                <UnitControl
                    units={controlUnits}
                    value={value}
                    className="plover-components-unit-slider-control__unit-control"
                    onChange={onChange}
                    onUnitChange={setSelectedUnit}
                    size={'__unstable-large'}
                />
                <RangeControl
                    hideLabelFromVision
                    className="plover-components-unit-slider-control__range-control"
                    value={parseQuantityAndUnitFromRawValue(value)[0]}
                    onChange={handleSliderChange}
                    initialPosition={0}
                    withInputField={false}
                    min={sliderMin}
                    max={sliderMax}
                    step={sliderStep}
                />
            </div>
        </fieldset>
    )
}
